import '../style/main.scss'

document.querySelector('.header__burger-menu').onclick = () => {
    document.querySelector('.pop-menu').classList.toggle('pop-menu_hide')
}

const closePopMenu = () => {
    document.querySelector('.pop-menu').classList.add('pop-menu_hide')
}
document.querySelector('.pop-menu__close').onclick = closePopMenu;
document.querySelector('.pop-menu__hiden').onclick = closePopMenu;
document.querySelectorAll('.pop-menu__item').forEach(it => {
    it.onclick = closePopMenu;
})